// @import url(http://fonts.googleapis.com/css?family=Permanent+Marker);

.header{
	margin-bottom: 40px;
	padding-top: 20px;
	text-align: center;

	img {
		display: inline;
		margin: 0 auto $margin-bottom;
		border-radius: 50%;
	}

	p {
		margin: 0 auto;
		max-width: 720px;
	}

	@include mq($bp-tablet) {
		padding: 0;
		display: flex;
		flex-direction: column;
		justify-content: center;
		// min-height: 80vh;
	}

	&__container {
		position: relative;
		padding: 0 15px;
		@include clearfix();
		max-width: 1180px;
		overflow: hidden;

		@include mq($bp-mobile, false){
			padding: 20px;
			margin: 0 20px 40px;
		}

		@include mq($bp-tablet, true){
			padding: 50px 0;
			margin: 0 50px;
		}

		@include mq($bp-widescreen, false){
			// padding: ($gutter * 3) 0;
			//padding: 4vw 0;
			margin: 0 auto;
		}
	}

	&__content{
		position: relative; z-index: 2;
		padding: $margin-bottom;
		margin: 0 auto;
		box-sizing: border-box;
		text-align: center;
	}
}

h1, h2{
	font-family: $font-title;
	text-transform: uppercase;
	font-size: 24px;
	font-weight: 900;
	line-height: .9;
	letter-spacing: -.01em;
	margin-bottom: .5em;
	color: $color-grey-lighter;

	.instagram &, #twitter &{
		text-align: center;
	}

	span{
		display: block;
		font-size: 80%;
		font-weight: 400;
		padding: .2em 0;
		color: $color-accent;
	}

	@include mq($bp-mobile, true){
		font-size: 24px;
		font-size: 3.8vw;
	}

	@include mq($bp-tablet, true){
		font-size: 28px;
		font-size: 3.2vw;
		span{
			font-size: 70%;
		}
	}

	@include mq($bp-widescreen, false){
		font-size: 48px;

		span{
			font-size: 60%;
		}
	}
}