.post{
	margin: 0 auto;
	@include clearfix();

	&__content{
		padding: 15px 0;
		margin: 0 15px;
		@include clearfix();
	}

	@include mq($bp-mobile, false){

		&__content{
			margin: 0 46px;
		}
	}

	@include mq($bp-tablet, true){
		background: rgba(0,0,0,.3);

		&__content{
			max-width: 1180px;
			padding: 50px 0;
			margin: 0 50px;
		}
	}

	@include mq($bp-widescreen, false){

		&__content{
			margin: 0 auto;
			padding: ($gutter * 3) 0;
			padding: 4vw 0;
		}
	}
}

// VIDEO
.video{
	float: left; display: block; clear: both;
	width: 100%;
	margin: 0;

	&__wrapper{
		position: relative; overflow: hidden;
		width: 100%; height: 0;
		margin: 0 auto; padding: 0 0 56.25%; // 16:9 ratio

		iframe, object, embed{
			position: absolute; top: 0; left: 0;
			width: 100% !important; height: 100% !important;
		}
	}
}