// BREAKPOINTS (MOBILE FIRST)
$bp-mobile				: 480;
$bp-smalltablet			: 600;
$bp-tablet				: 768;
$bp-desktop				: 1024;
$bp-widescreen			: 1280;

// AFMETINGEN
$gutter					: 20px;
$margin-bottom			: 20px;

// KLEUREN
$color-black			: rgb(0, 0, 0);
$color-grey-darker		: tint($color-black, 10);
$color-grey-dark		: tint($color-black, 30);
$color-grey				: tint($color-black, 50);
$color-grey-light		: tint($color-black, 70);
$color-grey-lighter		: tint($color-black, 90);
$color-white			: rgb(255, 255, 255);

$color-body 			: #333;
$color-text 			: $color-grey;

$color-gold				: #bca474;
$color-hover			: $color-gold;
$color-accent			: shade($color-gold, 30);

$color-alternate		: #8a2f62;

// TYPOGRAFIE
$font-lineheight		: 1.5em;
$font-lineheight-alt	: 1.1em; // WORDT OA GEBRUIKT VOOR ONDERSCHRIFT, TABELLEN, BUTTONS, OPSOMMINGEN
$font-lineheight-title	: 1.2em; // WORDT GEBRUIKT VOOR TITELS

$font-family			: "ff-meta-serif-web-pro", serif;
$font-title				: "brandon-grotesque", sans-serif;
$font-code				: Monaco, Consolas, "Lucida Console", monospace;
