.instagram{
	@include clearfix();
	margin: 0 10px;
	padding-bottom: 30px;

	@include mq($bp-mobile, false){
		margin: 0 40px;
		padding-bottom: 100px;
		// padding-bottom: 200px;
		// padding-bottom: 20vh;
	}

	@include mq($bp-widescreen, true){
		max-width: 1200px;
		margin: 0 auto;
	}

	&__item{
		float: left; display: none;
		width: 50%;

		.show &{
			&--1, &--2, &--3, &--4{
				display: inline;
			}

			@include mq($bp-mobile, false){
				width: 33.333%;

				&--5, &--6{
					display: inline;
				}
			}

			@include mq($bp-tablet, true){
				width: 25%;

				&--5, &--6, &--7, &--8{
					display: inline;
				}
			}
		}
	}

	&__link{
		position: relative;
		display: block;
		margin: 5px;
		border: 0 !important;
		background: $color-grey-darker;
		overflow: hidden;
		//box-shadow: 0 0 50px rgba(0,0,0,.5);

		img, video{
			-webkit-transform: translateZ(0);
			transition: opacity .4s ease-in-out, transform 1s ease-in-out;
			opacity: 1;
		}

		@include mq($bp-tablet, true){
			margin: 10px;
		}

		.no-touch &--video{
			img{
				display: none;
			}
		}

		.touch &--video{
			video{
				display: none;
			}
		}

		.touch &--video span, .no-video &--video span{
			position: absolute; left: 50%; top: 50%;
			z-index: 2;
			height: 40px; width: 40px;
			margin-left: -20px; margin-top: -20px;
		}

		&__image, &__video{
			position: relative; z-index: 1;
			display: block;
			width: 100%;
		}

		&__caption{
			display: none;
		}

		&:hover, &:focus{
			img, video{
				opacity: .7;
				transform: scale(1.1);
			}
		}
	}
}