#twitter {
	margin: 0;
	padding: 0 5px 100px;
	//padding: 0 5px 16vh;
	display: none;

	&.show {
		display: block;
	}

	@include mq($bp-mobile) {
		margin: 0 30px; 
	}

	@include mq($bp-smalltablet) {
		margin: 0 35px;
	}

	@include mq($bp-widescreen) {
		max-width: 1200px;
		margin: 0 auto;
	}
}

#tweets {
	list-style: none;
	@include clearfix();
}

.tweet {
	margin: 0;
	width: 100%;
	display: none;

	&--1, &--2{display: inline;}

	a{
		//position: absolute; left: 5px; right: 0; bottom: 0; top: 0;
		position: relative;
		border: 2px solid $color-grey-darker;
		border-color: rgba(0,0,0,0);
		color: $color-grey;
		//border: 0;
		display: block;
		margin: 0 10px 10px;
		padding: 50px 10px 15px;
		text-align: left;
		@include font-size(16);
		line-height: 1.4;
		border-radius: 3px;
		background: $color-black;
		background: rgba(0,0,0,.3);
		transition: border .4s ease-in-out, background .4s ease-in-out, color .4s ease-in-out;
		//height: 200px;

		span{
			position: absolute; left: 10px; top: 10px;
			display: block;
			font-size: 80%;
			font-family: $font-title;
			font-weight: 900;
			color: $color-accent;
			padding-bottom: 5px;
			border-bottom: 3px solid $color-accent;
		}

		&:hover, &:focus{
			//padding: 10px;
			//border: 5px solid #f8f8f8;
			background: rgba(0,0,0,.3);
			color: $color-grey-light;
			border-color: $color-grey-dark;
			border-color: rgba(0,0,0,.3);
		}
	}

	@include mq($bp-smalltablet){
		float: left;
		width: 50%;

		a{
			margin: 5px;
		}
	}

	@include mq($bp-tablet){
		width: 50%;

		&--3, &--4{display: inline;}

		a{
			margin: 10px;
			@include font-size(18);
		}
	}

	@include mq($bp-desktop, true){
		float: left;
		width: 33.33%;

		&--5, &--6{display: inline;}

		a{
			margin: 10px;
			@include font-size(20);
		}
	}
}